import request from '../../utils/request.js'

export default {
	data() {
		return {
			kingKong: {}
		}
	},
	async mounted() {
		const res = await request('/getHomeData')
		if(!res) {
			this.kingKong = {
				"kingKongModule": {
					"kingKongList": [
						{
						  "text": "居家生活",
							"L1Id": 111
						},
						{
						  "text": "服饰鞋包",
							"L1Id": 222
						},
						{
						  "text": "美食酒水",
							"L1Id": 333
						}
					]
				}
			}
		}else {
			this.kingKong = res.data
		}
	}
}
